export default {
	data() {
		return {
			index: 0,
			imgList: [],
			picker: ['大床房', '单人间', '双人间'],
			isActive: false,
			roomInfo: {
				type: "大床房"
			},
			is_edit: false
		};
	},
	onLoad(option) {
		this.is_edit = option.is_edit;
		console.log(this.is_edit)
		if(option.id){
			this.getRoomDetail(option.id);
		}
	},
	methods: {
		getRoomDetail(id) {
			uni.request({
				url: this.prefix + "/getRoomDetail",
				data: {
					rid: id
				},
				success: (res) => {
					this.roomInfo = res.data.data
					if (this.roomInfo.isActive == 'Y') {
						this.isActive = true
					}
					this.roomInfo.images.forEach((image) => {
						this.imgList.push(image.url)
					});
					[this.roomInfo.bedRoomCount, this.roomInfo.bathRoomCount, this.roomInfo.peopleCount, this.roomInfo.bedCount] = [
						this.roomInfo.propertyInfo.bedRoomCount, this.roomInfo.propertyInfo.bathRoomCount, this.roomInfo.propertyInfo
						.peopleCount, this.roomInfo.propertyInfo.bedCount
					]
				},
			})
		},
		PickerChange(e) {
			this.index = e.detail.value
			this.roomInfo.type = this.picker[this.index]
		},
		joinActive(e) {
			this.isActive = e.detail.value
		},
		ChooseImage() {
			uni.chooseImage({
				count: 3, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if(this.roomInfo.id){
						uni.uploadFile({
							url: this.prefix + "/doSaveRoomImages",
							filePath: res.tempFilePaths[0],
							name: 'file',
							header: {
								"Content-Type": "multipart/form-data"
							},
							formData: {
								rid: this.roomInfo.id
							},
							success: (res) => {
								if (res.data.code === 200) {
									uni.showToast({
										title: res.data.code,
										icon: "none"
									})
								} else {
									uni.showToast({
										title: res.data.code,
										icon: "none"
									})
								}
							}
						})
					}
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '确认删除吗',
				cancelText: '再看看',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						let url = this.imgList[e.currentTarget.dataset.index];
						if(this.roomInfo.id){
							uni.request({
								url: this.prefix + "/merchant/delImage",
								data: {
									url: url
								}
							})
						}
						this.imgList.splice(e.currentTarget.dataset.index, 1);
					}
				}
			})
		},
		sub_edit() {
			uni.showLoading({
				title: "提交中"
			})
			this.roomInfo.isActive = this.isActive?"off":"on"
			uni.request({
				url: this.prefix + "/merchant/doSaveRoom",
				data: {
					mid: this.getMerchantInfo().id,
					roomInfo: JSON.stringify(this.roomInfo)
				},
				success: (res) => {
					if (res.data.code === 200) {
						setTimeout(() => {
							uni.hideLoading();
							uni.showToast({
								title: "修改成功",
								icon: "success"
							});
						}, 800);
						setTimeout(()=>{uni.reLaunch({url:"./roomManage"})},1500)
					} else {
						uni.showToast({
							title: "修改异常",
							icon: "none"
						})
					}
				}
			})
		},
		sub_add(){
			uni.showLoading({
				title: "提交中"
			})
			this.roomInfo.isActive = this.isActive?"off":"on"
			var arr = Object.keys(this.roomInfo);
			if(arr.length < 10){
				uni.showToast({
					title:"请填写必填字段",
					icon:"none"
				})
				return 
			}
			uni.request({
				url: this.prefix + "/merchant/doSaveRoom",
				data: {
					mid: this.getMerchantInfo().id,
					roomInfo: JSON.stringify(this.roomInfo)
				},
				success: (res) => {
					if (res.data.code === 200) {
						this.imgList.forEach(image=>{
							uni.uploadFile({
							    url:this.prefix + "/doSaveRoomImages", 
								filePath: image,
								name: "file",
							    formData: {
									rid: res.data.data
								},
							    header:{"Content-Type": "multipart/form-data"},
							})
						})
						setTimeout(() => {
							uni.hideLoading();
							uni.showToast({
								title: "发布成功",
								icon: "success"
							});
						}, 800)
						setTimeout(()=>{uni.reLaunch({url:"./roomManage"})},1500)
					} else {
						uni.showToast({
							title: "发布异常",
							icon: "none"
						})
					}
				}
			})
		}
	}
}
